@use "../../styles/index.scss" as *;

.image {
  max-width: 100%;
  height: auto;
  display: block;
  transition: opacity 0.3s ease;

  &--loading {
    opacity: 0.5;
    filter: blur(2px);
  }

  &--error {
    opacity: 0.7;
  }

  // /   
  &--landscape {
    //    ( > 1.1)
  }

  &--portrait {
    //    ( < 0.9)
  }

  &--square {
    //    ( 0.9 ~ 1.1)
  }

  //   (   )
  &--fallback {
    background-color: var(--color-card);
    border: 2px dashed var(--color-border);
    border-radius: px-to-rem(8);
    object-fit: cover;
  }
}


// 
@media (max-width: 768px) {
  .image {
    &--fallback {
      min-height: px-to-rem(100);
    }

    .image__fallback-content {
      padding: px-to-rem(12);
    }

    .image__fallback-icon {
      font-size: 1.5rem;
    }
  }
}

