@use "../../styles/index.scss" as *;

.guide-preview--carousel {
  width: 100%;
  .swiper {
    padding-bottom: px(28);
  }

  .swiper-button-prev,
  .swiper-button-next {
    color: var(--color-accent);
  }

  .swiper-pagination-bullet {
    background: var(--color-border);
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background: var(--color-accent);
  }
}

.carousel-options {
  margin-top: px(14);
  padding: px(14);
  border: px(1) solid var(--color-border);
  border-radius: px(12);
  background: #f8f9fb;
  display: grid;
  gap: px(8);

  h5 {
    margin: 0;
    font-size: 0.95rem;
    color: var(--color-text);
  }

  ul {
    margin: 0;
    padding-left: px(18);
    display: grid;
    gap: px(4);
    color: var(--color-muted);
    li strong {
      color: var(--color-text);
      margin-right: 4px;
    }
  }

  &__tip {
    font-size: 0.88rem;
    color: var(--color-text);
    background: #eef2f7;
    border-radius: px(8);
    padding: px(10) px(12);
  }
}

.carousel-card {
  border: px(1) solid var(--color-border);
  border-radius: px(12);
  overflow: hidden;
  background: var(--color-card);
  box-shadow: 0 px(8) px(20) rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: column;

  &__image {
    position: relative;
    width: 100%;
    min-height: px(200);
    background: var(--color-bg);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  &__image-element {
    width: 100%;
    height: auto;

    &.image--landscape {
      width: 100%;
      height: auto;
    }

    &.image--portrait {
      width: auto;
      height: 100%;
    }

    &.image--square {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    padding: px(20);
  }

  h4 {
    margin: 0 0 px(8) 0;
    color: var(--color-text);
  }

  p {
    margin: 0;
    color: var(--color-muted);
  }
}

